<template>
  <v-container :style="$vuetify.breakpoint.mdAndUp === true ? 'max-width: 570px; margin-top:130px' : 'max-width: 390px; margin-top:20px; display:flex; flex-direction:column; justify-content:space-between; height:100vh;' ">
    <v-row justify="center">
      <v-spacer></v-spacer>
      <v-col cols="12" class="my-4" style="display: flex; justify-content: center">
        <h1 style="color:#384FFE; text-align: center ;font-size: 2.4rem">INICIO DE SESIN</h1>
      </v-col>
      <v-spacer></v-spacer>
    </v-row>
    <v-row>
      <v-col>
        <v-text-field
          v-model="correoInst"
          :label="'Correo Institucional'"
          outlined
          color="#384FFE"
          style="height: 50px;"
          :dense = true
          type = "text"
          :rules="[]"
        ></v-text-field>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <v-text-field
          v-model="password"
          :label="'Contrasea'"
          outlined
          color="#384FFE"
          style="height: 50px;"
          :dense = true
          type = "password"
          :rules="[]"
        ></v-text-field>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <v-dialog
          transition="dialog-bottom-transition"
          max-width="600"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-btn  :style="$vuetify.breakpoint.mdAndUp === true ? 'width: 100%; background: #384FFE; color: white; border-radius: 6px; cursor:pointer; font-weight: bold; font-size: 18px; box-shadow: 1px 1px 3px black'
            : 'width: 100%; background: #384FFE; color: white; border-radius: 6px; cursor:pointer; font-weight: bold; font-size:18px; box-shadow: 1px 1px 3px black'"
                    class="py-1"
                    @click="LogIn"
                    v-bind="attrs"
                    v-on="on"
            >Iniciar Sesin</v-btn>
          </template>
          <template v-slot:default="dialog">
            <v-card v-if="error.length > 0">
              <v-toolbar
                color="primary"
                dark
              >Error</v-toolbar>
              <v-card-text>
                <div class="text-h4 pa-12" v-if="error === 'Firebase: The email address is badly formatted. (auth/invalid-email).'">
                  Formato de Email invalido
                </div>
                <div class="text-h4 pa-12" v-if="error === 'Firebase: There is no user record corresponding to this identifier. The user may have been deleted. (auth/user-not-found).'">
                  Email no registrado
                </div>
                <div class="text-h4 pa-12" v-if="error === 'Firebase: A non-empty password must be provided (auth/missing-password).'">
                  Una contrasea debe ser ingresada
                </div>
                <div class="text-h4 pa-12" v-if="error === 'Firebase: The password is invalid or the user does not have a password. (auth/wrong-password).'">
                  Contrasea incorrecta
                </div>
              </v-card-text>
              <v-card-actions class="justify-end">
                <v-btn
                  text
                  @click="dialog.value = false"
                >Close</v-btn>
              </v-card-actions>
            </v-card>
          </template>
        </v-dialog>
      </v-col>
    </v-row>
    </v-container>
</template>

<script>
import firebase from 'firebase/compat'
export default {
  name: 'LogIn',
  data () {
    return {
      correoInst: '',
      password: '',
      error: ''
    }
  },
  methods: {
    async LogIn () {
      try {
        await firebase.auth().signInWithEmailAndPassword(this.correoInst.toUpperCase(), this.password).then(() => {
          this.$router.push('/')
        })
      } catch (e) {
        console.log(e.message)
        this.error = e.message
      }
    }
  }
}
</script>

